import { Avatar, Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns, GridRowData } from "@material-ui/data-grid";
import { Edit } from "@material-ui/icons";
import { RouteComponentProps } from "@reach/router";
import axios from "axios";

import React, { ReactElement } from "react";
import { useEffect } from "react";
import { useState } from "react";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import PageTitle from "../common/PageTitle";
import { filterAllKeys } from "../../helpers/FilterAllKeys";
import QuickSearchToolbar from "../common/QuickSearchToolbar";

interface Props extends RouteComponentProps {}

export default function MyWorkersPage(props: Props): ReactElement {
  const [workers, setWorkers] = useState<IWorkerTwoYear[]>([]);
  const [searchText, setSearchText] = useState("");
  const [rows, setRows] = useState<GridRowData[]>([]);

  const requestSearch = (searchValue: string) => {
    setSearchText(searchValue);

    if (searchValue === "") {
      setRows(workers);
    } else {
      const filteredRows = filterAllKeys({
        array: workers,
        searchValue: searchValue,
      });
      setRows(filteredRows);
    }
  };

  const columns: GridColumns = [
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Worker CV",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                if (props.navigate) props.navigate("edit/" + params.row._id);
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },
    // {
    //   field: "id",
    //   sortable: true,
    // },
    {
      field: "referenceId",
      headerName: "REF",
      sortable: true,
      width: 200,
    },
    {
      field: "avatar",
      headerName: "Pic",
      disableColumnMenu: true,
      renderCell: (params) => {
        const folder: S3Folder = "2y/sm";
        const src =
          "/" +
          folder +
          "/" +
          params.row._id +
          "." +
          params.row.smallPictureFileExt;
        return (
          <Box>
            <Avatar src={process.env.REACT_APP_IMAGES_ROOT + src} />
          </Box>
        );
      },
    },

    {
      field: "nationality",
      headerName: "Nationality",
      width: 200,
      // valueGetter: (params) => {
      //   const n: INationality = params.row.nationality;
      //   return n.title;
      // },
    },
    {
      field: "name",
      headerName: "Worker Name",
      sortable: true,
      width: 250,
    },
    {
      field: "jobTitle",
      headerName: "Job Title",
      sortable: true,
      description: "Job title of the worker",
      width: 200,
    },
    {
      field: "hidden",
      headerName: "Hidden",
      width: 130,
    },
    {
      field: "collected",
      headerName: "Collected",
      width: 140,
    },
    // {
    //   field: "age",
    //   headerName: "Age",
    //   sortable: false,
    // },
    // {
    //   field: "maritalStatus",
    //   headerName: "Marital Status",
    //   sortable: true,
    //   width: 200,
    // },
  ];

  // const workers: IWorkerTwoYear[] = WorkerTwoYearFactory.buildList(14);
  useEffect(() => {
    axios
      .get<IResponse<IWorkerTwoYear[]>>(
        process.env.REACT_APP_API_ROOT + "/be/workers-two-year",
        { withCredentials: true }
      )
      .then((result) => {
        setWorkers(result.data.data);
        setRows(result.data.data);
      });
  }, [setWorkers]);

  return (
    <Box>
      <PageTitle title="My Workers" />
      <Box height={700}>
        <DataGrid
          rows={rows}
          columns={columns}
          pageSize={15}
          components={{ Toolbar: QuickSearchToolbar }}
          componentsProps={{
            toolbar: {
              value: searchText,
              onChange: (event: any) => requestSearch(event.target.value),
              clearSearch: () => requestSearch(""),
            },
          }}
          // checkboxSelection
        />
      </Box>
    </Box>
  );
}
