import { Avatar, Box, Button, ButtonGroup } from "@material-ui/core";
import { DataGrid, GridColumns, GridRowData } from "@material-ui/data-grid";
import { RouteComponentProps } from "@reach/router";
import { ReactElement, useEffect, useState } from "react";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { Edit } from "@material-ui/icons";
import PageTitle from "../common/PageTitle";
import { IRequest } from "estreeh-common-library/src/interfaces/IRequest";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import dateFormat from "dateformat";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import axios from "axios";
// import { useStoreActions } from "../../helpers/StoreTypedHooks";
import { useStoreState } from "../../helpers/StoreTypedHooks";
import QuickSearchToolbar from "../common/QuickSearchToolbar";
import { filterAllKeys } from "../../helpers/FilterAllKeys";

interface Props extends RouteComponentProps {
  // config: IApiResConfig;
}

export default function MyRequestsPage(props: Props): ReactElement {
  //TODO: "New requests" page or filter
  //TODO: "Pending for Action" page or filter

  const [requests, setRequests] = useState<any[]>([]);
  const config = useStoreState((s) => s.config.item);
  const [searchText, setSearchText] = useState("");
  const [rows, setRows] = useState<GridRowData[]>([]);
  // const requests = useStoreState((s) => s.requests.items);
  // const setRequests = useStoreActions((a) => a.requests.setItems);

  const requestSearch = (searchValue: string) => {
    setSearchText(searchValue);

    if (searchValue === "") {
      setRows(requests);
    } else {
      const filteredRows = filterAllKeys({
        array: requests,
        searchValue: searchValue,
      });
      setRows(filteredRows);
    }
  };

  const columns: GridColumns = [
    {
      sortable: false,
      disableColumnMenu: true,
      field: "actions",
      headerName: "Actions",
      width: 140,
      renderCell: (params) => (
        <Box textAlign="center">
          <ButtonGroup color="primary" variant="outlined">
            <Button
              size="small"
              variant="outlined"
              color="primary"
              onClick={() => {
                console.log(params.row);
                if (props.navigate) props.navigate(`update/${params.row.id}`);
              }}
            >
              <Edit fontSize="small" />
            </Button>
            {/* <Button size="small">
              <ListAlt fontSize="small" />
            </Button> */}
          </ButtonGroup>
        </Box>
      ),
    },
    {
      field: "created",
      headerName: "Date",
      sortable: true,
      width: 140,
      valueFormatter: (params) => {
        return dateFormat(params.row.created, "dd mmm yy");
      },
    },
    {
      field: "avatar",
      headerName: "Pic",
      disableColumnMenu: true,
      renderCell: (params) => {
        const folder: S3Folder = "2y/sm";
        const worker: IWorkerTwoYear = params.row.workerTwoYear;
        const src =
          "/" + folder + "/" + worker._id + "." + worker.smallPictureFileExt;
        return (
          <Box>
            <Avatar src={process.env.REACT_APP_IMAGES_ROOT + src} />
          </Box>
        );
      },
    },
    {
      field: "Worker Name",
      sortable: true,
      width: 200,
      valueGetter: (params) => {
        const w: IWorkerTwoYear = params.row.workerTwoYear;
        return w.name;
      },
    },

    {
      field: "statusCode",
      headerName: "Status",
      sortable: true,
      width: 180,
      // valueGetter: (params) => {
      //   return
      // },
      renderCell: (params) => {
        const item = config?.statusCodesTwoYear.find(
          (x) => x.code === params.row.statusCode
        );
        return (
          <Box
            px={1}
            py={0.5}
            bgcolor={item?.bgColor}
            borderRadius={50}
            whiteSpace="normal"
            lineHeight={1}
            fontSize="90%"
            width="100%"
            textAlign="center"
          >
            {item?.title}
          </Box>
        );
      },
    },

    {
      field: "fullName",
      headerName: "Applicant",
      sortable: true,
      width: 220,
    },

    {
      field: "mobileNumber",
      headerName: "Mobile",
      sortable: true,
      width: 140,
    },
    {
      field: "workerReferenceId",
      headerName: "REF",
      sortable: true,
      width: 130,
    },
    {
      field: "Tracking",
      sortable: false,
      width: 130,
      valueGetter: (params) => {
        return params.row.trackingCode;
      },
    },
    {
      field: "workerJobTitle",
      headerName: "Job",
      sortable:false,
    },
    {
      field: "workerNationality",
      headerName: "Nation.",
      sortable:false,
    },
    // {
    //   field: "specialNotes",
    //   sortable: true,
    //   width: 200,
    // },
  ];

  useEffect(() => {
    console.log("Preparing to get Requests");
    axios
      .get<IResponse<IRequest[]>>(
        process.env.REACT_APP_API_ROOT + "/be/requests",
        { withCredentials: true }
      )
      .then((requests) => {
        console.log(`Got ${requests.data.data.length} requests`);
        // setRequests({
        //   payload: requests.data.data,
        // });
        // setRequests(requests.data.data);
        const origData = requests.data.data;
        const data = origData.map((v) => {
          const a = {
            ...v.applicationForm,
            trackingCode: v.trackingCode,
            // requestId: v.id,
            id: v._id,
            workerTwoYear: v.workerTwoYear,
            statusCode: v.statusCode,
            created: v.created,
            workerName: v.workerTwoYear?.name,
            workerNationality: v.workerTwoYear?.nationality,
            workerJobTitle: v.workerTwoYear?.jobTitle,
            workerReferenceId: v.workerTwoYear?.referenceId,
            applicantName: v.applicationForm.fullName,
            applicantMobile: v.applicationForm.mobileNumber,
          };
          return a;
        });
        setRequests(data);
        setRows(data);
      });
  }, [setRequests]);
  // const applications: IApplicationForm[] = ApplicationFormFactory.buildList(14);

  return (
    <Box>
      <PageTitle title="My Requests" />
      <Box height={700}>
        <DataGrid
          sortModel={[
            {
              field: "created",
              sort: "desc",
            },
          ]}
          disableSelectionOnClick={true}
          rows={rows}
          columns={columns}
          pageSize={15}
          components={{ Toolbar: QuickSearchToolbar }}
          componentsProps={{
            toolbar: {
              value: searchText,
              onChange: (event: any) => requestSearch(event.target.value),
              clearSearch: () => requestSearch(""),
            },
          }}
          // checkboxSelection
        />
      </Box>
    </Box>
  );
}
