import { Box, Typography } from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React, { ReactElement, useEffect, useState } from "react";
import { IBackendField } from "estreeh-common-library/src/interfaces/IBackendField";
import { Formik } from "formik";
import * as Yup from "yup";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";

import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { IProcessTemplate } from "estreeh-common-library/src/interfaces/IProcessTemplate";
import { toast } from "react-toastify";
import AddEditWorkerForm from "./AddEditWorkerForm";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import axios, { AxiosResponse } from "axios";
import PageTitle from "../common/PageTitle";

interface Props extends RouteComponentProps {
  id?: string;
}

interface CompletedProps {
  result: AxiosResponse<IResponse>;
  navigateOut: boolean;
  setSubmitting: Function;
}

export default function AddEditWorkerPage(props: Props): ReactElement {
  const [loading, setLoading] = useState(true);
  // const [saving, setSaving] = useState(false);
  const [processTemplates, setProcessTemplates] = useState<IProcessTemplate[]>(
    []
  );
  const [processTemplateId, setProcessTemplateId] = useState("");
  // const [pendingRequests, setPendingRequests] = useState(9999);
  // const [formikSubmitting, setFormikSubmitting] = useState<
  //   ((isSubmitting: boolean) => void) | undefined
  // >(undefined);

  const [worker, setWorker] = useState<Partial<IWorkerTwoYear>>({
    addressOriginalCountry: {
      city: "",
    },
    // age: 0,
    // agencyId: "",
    // created: new Date(),
    // dateOfBirth: new Date(),
    // modified: new Date(),
    // monthlySalary: 0,
    // numberOfChildren: 0,
    // experienceYears: 0,
    // heightCm: 0,
    languageSkillArabic: "Fair",
    languageSkillEnglish: "Fair",
    agencyRemarks: "",
    cityOfBirth: "",
    // contractPeriod: 2,
    demoLanguageSkillsText: "",
    educationalLevel: "",
    hidden: false,
    collected: false,
    isLocalTransfer: false,
    jobExperiences: [],
    jobTitle: "",
    localTransferReason: "",
    maritalStatus: "",
    name: "",
    nationality: "",
    otherSkills: [],
    phone: "",
    processFees: 0,
    processTemplateId: "",
    referenceId: "",
    remarks: "",
    weightKg: 0,
  });

  function getTotalProcessFees() {
    const t = processTemplates.find((x) => x._id === processTemplateId);
    if (t === undefined) return 0;
    return t.totalFeesBhd;
  }

  function getProcessTemplates() {
    axios
      .get<IResponse<IProcessTemplate[]>>(
        process.env.REACT_APP_API_ROOT + "/be/process-templates",
        { withCredentials: true }
      )
      .then((result) => {
        setProcessTemplates(result.data.data);
        setLoading(false);
      });
  }

  useEffect(() => {
    if (loading) {
      if (props.id !== undefined) {
        axios
          .get<IResponse<IWorkerTwoYear>>(
            process.env.REACT_APP_API_ROOT + "/be/workers-two-year/" + props.id,
            { withCredentials: true }
          )
          .then((response) => {
            let worker = response.data.data;
            setWorker(worker);
            setProcessTemplateId(worker.processTemplateId);
            getProcessTemplates();
          });
      } else {
        getProcessTemplates();
      }
    }
  }, [loading, props.id]);

  const templateFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "processTemplateId",
      variant: "select",
      selectOptions: processTemplates.map((p) => ({
        value: p._id ? p._id : "NOVAL",
        title: p.title,
      })),
      value: processTemplateId,
      onChange: (e) => {
        setProcessTemplateId(e.target.value);
      },
      ignoreUnboundCheck: true,
    },

    {
      name: "processFees",
      readOnly: true,
      value: getTotalProcessFees(),
      ignoreUnboundCheck: true,
    },
  ];

  const formDisabled = loading;

  function uploadPicture(props: {
    _id: string;
    file: any;
    folder: S3Folder;
    result: any;
    setSubmitting: (isSubmitting: boolean) => void;
    navigateOut: boolean;
  }) {
    let formData = new FormData();
    formData.append("_id", props._id);
    formData.append("folder", props.folder);
    formData.append("file", props.file);
    return axios.request<IResponse>({
      method: "PUT",
      url: process.env.REACT_APP_API_ROOT + "/be/workers-two-year/picture",
      data: formData,
      withCredentials: true,
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
    // .then((result) => {
    //   toast.success(result.data.message);
    //   completed(props.result, props.navigateOut, props.setSubmitting);
    // });
  }

  // const updatePending = (increment: number, setSubmitting: Function) => {
  // let p = pendingRequests + increment;

  // console.log("Pending requests ::", pendingRequests, "now:", p);
  // if (p <= 0) {
  // setSubmitting(false);

  // p = 9999;
  // } else {
  // setSubmitting(true);
  // }
  // setPendingRequests(p);
  // };

  function completed(props: CompletedProps) {
    const message = props.result.data.message
      ? props.result.data.message
      : "No Message.";
    toast.success(message);
    // updatePending(-1, props.setSubmitting);
  }

  async function saveData(values: any, isNew: boolean) {
    const url = process.env.REACT_APP_API_ROOT + "/be/workers-two-year";
    const method = isNew ? "POST" : "PUT";
    return axios.request<IResponse<IWorkerTwoYear>>({
      url,
      method,
      data: values,
      withCredentials: true,
    });
  }

  return (
    <Box>
      <PageTitle title={props.id===undefined? "Add a New Worker" : "Edit Worker"} />
      {loading && (
        <Typography>
          Loading <strong>Worker Details</strong> and{" "}
          <strong>Process Templates...</strong>
        </Typography>
      )}
      {!loading && processTemplates.length < 1 && (
        <Typography>
          To add a worker, you must add one <b>Process Template</b> at least.
          <br />
          <br />
          To do that, please access the "Process Templates" using the left-side
          navigation, or{" "}
          <Link to="/panel/agency/settings/process-templates">click here</Link>.
        </Typography>
      )}
      {!loading && processTemplates.length > 0 && (
        <>
        
        <Formik
          initialValues={worker}
          validationSchema={Yup.object({
            name: Yup.string().required("Required"),
            experienceYears: Yup.number().required("Required"),
            // contractPeriod: Yup.number().required("Required"),
            // monthlySalary: Yup.number().required("Required"),
            hidden: Yup.bool().required("Required"),

            // processTemplateId: Yup.string().required("Required"),
          })}
          onSubmit={(formValues, { setSubmitting }) => {
            setSubmitting(true);
            // setSaving(true);

            const values: Partial<IWorkerTwoYear> = {
              ...formValues,
              processTemplateId,
              processFees: getTotalProcessFees(),
            };

            const isNew = props.id === undefined;

            let promises: Promise<any>[] = [];

            // let requests = 1;
            // if (formValues.uploadSmallPicture !== undefined) requests++;
            // if (formValues.uploadBigPicture !== undefined) requests++;

            // console.log("Setting requests to:", requests, "(Problem?)");
            // setPendingRequests(requests);

            saveData(values, isNew)
              .then((result) => {
                // completed({
                //   result,
                //   navigateOut: isNew,
                //   setSubmitting,
                // });
                const _id = result.data.data._id;
                if (_id !== undefined) {
                  if (formValues.uploadSmallPicture !== undefined) {
                    promises.push(
                      uploadPicture({
                        _id,
                        file: formValues.uploadSmallPicture,
                        folder: "2y/sm",
                        navigateOut: isNew,
                        result,
                        setSubmitting,
                      })
                    );
                    // uploadPicture({
                    //   _id,
                    //   file: formValues.uploadSmallPicture,
                    //   folder: "2y/sm",
                    //   navigateOut: isNew,
                    //   result,
                    //   setSubmitting,
                    // }).then((resultPic) => {
                    //   // setSubmitting(false);
                    //   completed({
                    //     result: resultPic,
                    //     navigateOut: isNew,
                    //     setSubmitting,
                    //   });
                    // });
                  }

                  if (formValues.uploadBigPicture !== undefined) {
                    promises.push(
                      uploadPicture({
                        _id,
                        file: formValues.uploadBigPicture,
                        folder: "2y/lg",
                        navigateOut: isNew,
                        result,
                        setSubmitting,
                      })
                    );
                    // uploadPicture({
                    //   _id,
                    //   file: formValues.uploadBigPicture,
                    //   folder: "2y/lg",
                    //   navigateOut: isNew,
                    //   result,
                    //   setSubmitting,
                    // }).then((resultPic) => {
                    //   // setSubmitting(false);
                    //   completed({
                    //     result: resultPic,
                    //     navigateOut: isNew,
                    //     setSubmitting,
                    //   });
                    // });
                  }
                }
                Promise.all(promises).finally(() => {
                  completed({
                    result,
                    navigateOut: isNew,
                    setSubmitting,
                  });
                  if (props.navigate !== undefined) {
                    if (isNew === true)
                      props.navigate("/panel/my-workers/success");
                    else props.navigate("/panel/my-workers");
                  }
                });
              })
              .catch((reason) => {
                toast.error(reason.message);
                // updatePending(-1000000, setSubmitting);
                // setSaving(false);
              });
          }}
        >
          {(formik) => (
            <>
              {/* {setFormikSubmitting(formik.setSubmitting)} */}
              <AddEditWorkerForm
                id={props.id}
                formik={formik}
                templateFields={templateFields}
                disabled={formDisabled}
                worker={worker}
              />
            </>
          )}
        </Formik>
        </>
      )}
    </Box>
  );
}
