import { Box, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import PageTitle from "../common/PageTitle";
import QRCode from "react-qr-code";
import { useStoreState } from "../../helpers/StoreTypedHooks";


interface Props extends RouteComponentProps {}

export default function AgencyQrCodePage(props: Props): ReactElement {

  const agency = useStoreState(s => s.agency.item);

  const qr = process.env.REACT_APP_FE_ROOT + "/agencies/contract/" + agency?.slug;

  return (
    <Box>
      <PageTitle title="QR Code Print" />
      <Box>
        Use this page to generate a printable QR Code that redirects customers
        to your own ESTREEH page!
      </Box>
      <Box my={2}>
        <Typography variant="h5">QR Code</Typography>
      </Box>
      <Box p={3}>
        <QRCode value={qr} size={400} />
      </Box>
      <Box px={3} py={1}>
        <a href={qr} target="_blank" rel="noreferrer">{qr}</a>
      </Box>
    </Box>
  );
}
