import { Box, IconButton, TextField } from "@material-ui/core";
import { Clear, Search } from "@material-ui/icons";
import React, { ReactElement } from "react";
import Branding from "../../config/Branding";

interface Props {
  value: string | number;
  onChange?: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
  clearSearch?: React.MouseEventHandler<HTMLButtonElement>;
}

export default function QuickSearchToolbar(props: Props): ReactElement {
  return (
    <Box
      p={2.5}
      pb={0}
      style={{
        // justifyContent: "space-between",
        // display: "flex",
        // alignItems: "flex-start",
        // flexWrap: "wrap",
        maxWidth:640,
      }}
    >
      <TextField
        variant="outlined"
        value={props.value}
        onChange={props.onChange}
        placeholder="Search"
        InputProps={{
          startAdornment: <Search fontSize="small" style={{ marginRight:8, color:Branding.primary }} />,
          endAdornment: (
            <IconButton
              title="Clear"
              aria-label="Clear"
              size="small"
              style={{ visibility: props.value ? "visible" : "hidden" }}
              onClick={props.clearSearch}
            >
              <Clear fontSize="small" style={{ color:Branding.text.warning }} />
            </IconButton>
          ),
        }}
        // sx={{
        //   width: {
        //     xs: 1,
        //     sm: "auto",
        //   },
        //   m: (theme) => theme.spacing(1, 0.5, 1.5),
        //   "& .MuiSvgIcon-root": {
        //     mr: 0.5,
        //   },
        //   "& .MuiInput-underline:before": {
        //     borderBottom: 1,
        //     borderColor: "divider",
        //   },
        // }}
      />
    </Box>
  );
}
