import { Box } from "@material-ui/core";
import { Link } from "@reach/router";
import React, { ReactElement } from "react";
import { INavItem } from "estreeh-common-library/src/interfaces/INavItem";
import Branding from "../../config/Branding";

import Logo from "../common/Logo";
import LeftNavItem from "./LeftNavItem";

interface Props {
  countRequests: number;
  countWorkers: number;
}

export default function LeftNav(props: Props): ReactElement {
  const navs: INavItem[] = [
    {
      key: "dash1",
      title: "Home",
      to: "/panel/dashboard",
      // icon: "las la-chart-pie",
      icon: "las la-home",
    },
    {
      key: "myrequests",
      title: "My Requests",
      to: "/panel/my-requests",
      icon: "las la-list-alt",
      count: props.countRequests,
    },

    {
      key: "myworkers",
      title: "My Workers",
      to: "/panel/my-workers",
      icon: "las la-users",
      count: props.countWorkers,
      // count:92,
      children: [
        {
          key: "mw-manageworkers",
          title: "Manage Workers",
          to: "/panel/my-workers",
          icon: "las la-users",
        },
        {
          key: "mw-addnewworker",
          title: "Add New Worker",
          to: "/panel/my-workers/add",
          icon: "las la-plus",
        },
      ],
    },

    //TODO: New link: QR Code

    {
      key: "agencysettings",
      title: "Agency Settings",
      icon: "las la-cog",
      children: [
        {
          key: "as-settings",
          title: "General Settings",
          to: "/panel/agency/settings",
          icon: "las la-server",
        },
        // {
        //   key: "as-nationalities",
        //   title: "Nationalities",
        //   to: "/panel/agency/settings/nationalities",
        // },
        {
          key: "as-processes",
          title: "Process Templates",
          to: "/panel/agency/settings/process-templates",
          icon: "las la-border-none",
        },
        // {
        //   key: "as-payment",
        //   title: "Payment Methods",
        //   to: "/panel/agency/settings/payment-methods",
        // },

        // {
        //   key: "as-terms",
        //   title: "Terms & Conditions",
        //   to: "/panel/agency/settings/terms",
        // },
        {
          key: "as-qr",
          title: "QR Code",
          to:"/panel/agency/settings/qr-code",
          icon: "las la-qrcode",
        },
        // {
        //   title: "Workflow",
        // },
      ],
    },
    // {
    //   title: "Archive",
    //   to: "/panel/archive",
    //   icon: "arrow_forward_ios",
    // },
    // {
    //   key: "logout",
    //   title: "Logout",
    //   to: "/login",
    //   icon: "logout",
    // },
  ];

  return (
    <Box position="fixed" width="16.6666667%" height="100%">
      <Box bgcolor={Branding.primary} color="white" height="100%">
        <Box p={2} bgcolor={Branding.lighter} textAlign="left">
          <Link to="/panel" style={{ display: "inline-block" }}>
            <Logo />
          </Link>
        </Box>

        <Box px={0} py={4}>
          {navs.map((nav) => (
            <LeftNavItem
              my={2}
              mx={1}
              navItem={nav}
              key={nav.title + "_" + nav.to}
            />
          ))}
        </Box>
      </Box>
    </Box>
  );
}
