import {
  Avatar,
  Box,
  Grid,
  GridSize,
  Typography,
} from "@material-ui/core";
import { navigate, RouteComponentProps } from "@reach/router";

import { ReactElement } from "react";
import PageTitle from "../common/PageTitle";
import DashboardCard from "./DashboardCard";
import { IUser } from "estreeh-common-library/src/interfaces/IUser";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import { useStoreState } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {
  user: IUser;
  // dashSummary: IApiResDashboardSummary | undefined;
  // agency: IAgency | null;
}

export default function DashboardPage(props: Props): ReactElement {
  const agency = useStoreState((s) => s.agency.item);
  const dashSummary = useStoreState((s) => s.dashboardSummary.item);
  const folder: S3Folder = "agency/sm";
  const cardGridProps: {
    md: GridSize;
    xs: GridSize;
  } = {
    md: 3,
    xs: 12,
  };

  return (
    <Box>
      <Grid container direction="row-reverse" spacing={2}>
        <Grid item md={3} xs={12}>
          <Box textAlign="center">
            <Avatar
              src={
                process.env.REACT_APP_IMAGES_ROOT +
                "/" +
                folder +
                "/" +
                agency?._id +
                "." +
                agency?.logoFileExt
              }
              variant="rounded"
              style={{
                width: 260,
                height: 220,
                marginLeft: "auto",
                marginRight: "auto",
              }}
            />
            <Typography>{agency?.title}</Typography>
            <Typography>{agency?.titleAr}</Typography>
            <Typography>CR: {agency?.cr}</Typography>
          </Box>
        </Grid>
        <Grid item md={9} xs={12}>
          <PageTitle title={`Agency Dashboard`} />
          <Typography>Welcome to the Estreeh agency portal.</Typography>
          <Typography>Please use the left menu to navigate.</Typography>
          <Box p={3}>
            <Grid container spacing={2} alignItems="center">
              <Grid item {...cardGridProps}>
                <DashboardCard
                  title="New Requests"
                  value={dashSummary?.newRequests}
                  onClick={() => {
                    navigate("my-requests")
                  }}
                />
              </Grid>
              <Grid item {...cardGridProps}>
                <DashboardCard
                  title="Pending Requests"
                  value={dashSummary?.pendingRequests}
                  onClick={() => {
                    navigate("my-requests")
                  }}
                />
              </Grid>
              <Grid item {...cardGridProps}>
                <DashboardCard
                  title="My Workers"
                  value={dashSummary?.myWorkers}
                  onClick={() => {
                    navigate("my-workers")
                  }}
                />
              </Grid>
              <Grid item {...cardGridProps}>
                <DashboardCard
                  title="Customer Reviews"
                  value={dashSummary?.customerReviews}
                />
              </Grid>
            </Grid>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}
