import { Box, Typography } from "@material-ui/core";
import React, { ReactElement, useEffect } from "react";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";
import Logo from "../common/Logo";
import MasterRouter from "./MasterRouter";

interface Props {}

export default function MasterData(props: Props): ReactElement {

  const dataLoaded = useStoreState((s) => s.dataLoaded.item);
  const loadAllInit = useStoreActions((a) => a.loadAllInit);

  useEffect(() => {
    if (dataLoaded === false) {
      console.log("Loading all data init");
      loadAllInit();
    }
  }, [dataLoaded, loadAllInit]);

  return (
    <>
      {dataLoaded === true && <MasterRouter />}
      {dataLoaded === false && (
        <Box p={2} textAlign="center">
          <Box py={5}>
            <Logo styles={{
              maxHeight:80,
              marginLeft:"auto",
              marginRight:"auto",
            }}/>
          </Box>
          <Typography variant="h5">Loading...</Typography>
        </Box>
      )}
    </>
  );
}
