import { FormikProps } from "formik";
import React, { ReactElement } from "react";
import PageTitle from "../common/PageTitle";
import BackendFieldSet from "../forms/BackendFieldSet";
import {
  complexions,
  educationLevels,
  jobTitles,
  maritalStatus,
  otherSkills,
  religions,
} from "../../config/FilterOptions";
import { IBackendField } from "estreeh-common-library/src/interfaces/IBackendField";
import IWorkerTwoYear from "estreeh-common-library/src/interfaces/IWorkerTwoYear";
import { Avatar, Grid } from "@material-ui/core";
import SubmitButton from "../forms/SubmitButton";
import { IBackendFieldSelectOption } from "estreeh-common-library/src/interfaces/IBackendFieldSelectOption";
import { nationalities } from "../../config/Nationalities";
import { S3Folder } from "estreeh-common-library/src/types/S3Folder";
import BackendCheckboxGroup from "../forms/BackendCheckboxGroup";

interface Props {
  formik: FormikProps<any>;
  id?: string;
  disabled: boolean;
  worker: Partial<IWorkerTwoYear>;
  templateFields: IBackendField[];
}

export default function AddEditWorkerForm({
  formik,
  disabled,
  id,
  worker,
  templateFields,
}: Props): ReactElement {
  const folderSm: S3Folder = "2y/sm";
  const folderLg: S3Folder = "2y/lg";

  const smallPicture =
    process.env.REACT_APP_IMAGES_ROOT +
    "/" +
    folderSm +
    "/" +
    worker._id +
    "." +
    worker.smallPictureFileExt;
  const bigPicture =
    process.env.REACT_APP_IMAGES_ROOT +
    "/" +
    folderLg +
    "/" +
    worker._id +
    "." +
    worker.bigPictureFileExt;

  const mainFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "name",
    },
    {
      name: "referenceId",
    },

    {
      name: "jobTitle",
      variant: "select",
      selectOptions: jobTitles.map((t) => ({
        value: t,
      })),
    },
    {
      name: "monthlySalary",
    },
    // {
    //   name: "contractPeriod",
    // },
    {
      name: "phone",
    },
    // {
    //   name:"hide",
    //   label:"Hide this worker",
    //   variant:"checkbox"
    // }
    {
      name: "isLocalTransfer",
      variant: "checkbox",
    },
  ];

  const localTransferFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "localTransferReason",
      variant: "textarea",
    },
  ];

  const hidingFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "hidden",
      label: "Hide this worker",
      variant: "switch",
    },
    {
      name: "collected",
      label: "Worker collected (will also hide)",
      variant: "switch",
    },
  ];

  const fileFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "uploadSmallPicture",
      variant: "file",
      avatarWidth: 80,
      buttonIcon: "photo_camera",
    },
    {
      name: "uploadBigPicture",
      variant: "file",
      avatarWidth: 200,
      buttonIcon: "photo_camera",
    },
  ];

  const agencyOnlyFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "agencyRemarks",
      variant: "textarea",
    },
  ];

  const personalFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "nationality",
      variant: "select",
      selectOptions: nationalities.map((n) => ({
        value: n,
      })),
    },
    {
      name: "experienceYears",
      type: "number",
    },

    {
      name: "educationalLevel",
      variant: "select",
      selectOptions: educationLevels.map((e) => ({
        value: e,
      })),
    },
    {
      name: "dateOfBirth",
      type: "date",
    },
    {
      name: "maritalStatus",
      variant: "select",
      // selectOptions: [{ value: "Single" }, { value: "Married" }],
      selectOptions: maritalStatus.map((m) => ({ value: m })),
    },
    {
      name: "age",
      type: "number",
    },
    {
      name: "weightKg",
    },
    {
      name: "complexion",
      variant: "select",
      selectOptions: complexions.map((c) => ({
        value: c,
      })),
      // selectOptions: [
      //   {
      //     value: "Light",
      //   },
      //   {
      //     value: "Brown",
      //   },
      //   {
      //     value: "Dark",
      //   },
      // ],
    },
    {
      name: "religion",
      variant: "select",
      selectOptions: religions.map((r) => ({ value: r })),
      // selectOptions: [
      //   {
      //     value: "None",
      //   },
      //   {
      //     value: "Buddhist",
      //   },
      //   {
      //     value: "Christian",
      //   },
      //   {
      //     value: "Hindu",
      //   },
      //   {
      //     value: "Muslim",
      //   },
      // ],
    },

    // {
    //   name: "livingTown",
    // },
    {
      name: "numberOfChildren",
      type: "number",
    },
    {
      name: "heightCm",
    },

    {
      name: "remarks",
      variant: "textarea",
      md: 9,
    },
  ];

  const languageOptions: IBackendFieldSelectOption[] = [
    {
      value: "Poor",
    },
    {
      value: "Fair",
    },
    {
      value: "Fluent",
    },
  ];

  const languageFields: IBackendField<IWorkerTwoYear>[] = [
    {
      name: "languageSkillEnglish",
      variant: "select",
      selectOptions: languageOptions,
    },
    {
      name: "languageSkillArabic",
      variant: "select",
      selectOptions: languageOptions,
    },
  ];

  // const skills = [
  //   "Baby Sitting",
  //   "Cooking",
  //   "Nursing",
  //   "Elder Caring",
  //   "Washing",
  //   "Ironing",
  // ];

  function picHelpClick() {
    alert(
      "To change picture please use the Upload Picture buttons near the end of this page."
    );
  }

  return (
    <form onSubmit={formik.handleSubmit}>
      {id !== undefined && (
        <Grid container spacing={2}>
          <Grid item>
            <Avatar
              src={smallPicture}
              variant="rounded"
              style={{
                width: 90,
                height: 90,
                cursor: "help",
              }}
              onClick={picHelpClick}
            />
          </Grid>
          <Grid item xs={9}>
            <PageTitle title={worker.name} />
          </Grid>
        </Grid>
      )}
      <BackendFieldSet
        fields={mainFields}
        header="Main Information"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={localTransferFields}
        md={7}
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={templateFields}
        header="Process Template"
        // formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={personalFields}
        header="Personal Details"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={hidingFields}
        header="Show/Hide Settings"
        helpText="WARNING: Enabling any of these options will hide the worker!"
        formik={formik}
        disabled={disabled}
      />
      <BackendFieldSet
        fields={languageFields}
        header="Language Skills"
        formik={formik}
        disabled={disabled}
      />

      <BackendFieldSet
        header="Skills"
        xs={12}
        formik={formik}
        disabled={disabled}
      >
        <BackendCheckboxGroup
          options={otherSkills}
          formik={formik}
          arrayFieldName="otherSkills"
        />
      </BackendFieldSet>
      <BackendFieldSet header="Pictures" xs={12}>
        <Grid container>
          <Grid item xs={4}>
            <Avatar
              src={smallPicture}
              variant="rounded"
              style={{
                width: 160,
                height: 160,
                cursor: "help",
              }}
              onClick={picHelpClick}
            />
          </Grid>
          <Grid item xs={4}>
            <Avatar
              src={bigPicture}
              variant="rounded"
              style={{
                width: 240,
                height: 320,
                cursor: "help",
              }}
              onClick={picHelpClick}
            />
          </Grid>
        </Grid>
      </BackendFieldSet>
      <BackendFieldSet
        fields={fileFields}
        formik={formik}
        disabled={disabled}
        // header="Picture Uploads"
        // helpText="Use the below buttons to change the current pictures."
      />
      <BackendFieldSet
        fields={agencyOnlyFields}
        header="Agency Only Information"
        helpText="This information will be visible only to the Agency. It will NOT be visible to the customers."
        md={7}
        formik={formik}
        disabled={disabled}
      />
      <SubmitButton
        title={formik.isSubmitting ? "Saving..." : "Save"}
        errors={formik.errors}
        type="submit"
        //TODO: The button does not get disabled.....
        disabled={disabled || formik.isSubmitting}
      />
    </form>
  );
}
