import { Box, Button, Grid, TextField } from "@material-ui/core";
import { Link, RouteComponentProps } from "@reach/router";
import React, { ReactElement, useState } from "react";
import Branding from "../../config/Branding";
import { useStoreActions } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {}

export default function LoginPage(props: Props): ReactElement {
  const login = useStoreActions((a) => a.login);

  const [loggingIn, setLoggingIn] = useState(false);
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");

  const loginFail = (reason?: string) => {
    setLoggingIn(false);
    if (reason !== undefined) console.log("Login error:", reason);
  };
  const onClick = () => {
    setLoggingIn(true);
    login({
      email,
      password,
      app: "agency-be",
    })
      .then((result) => {
        const user = result;
        if (user === undefined || user.agencyId === undefined) {
          loginFail("User is not authorized to use this platform.");
        }
      })
      .catch((reason) => {
        loginFail(reason);
      });
  };

  return (
    <Box>
      <Box
        p={3}
        maxWidth={440}
        marginLeft="auto"
        marginRight="auto"
        my={5}
        boxShadow={Branding.shadow.niceShadow}
        borderRadius={Branding.borderRadius.niceRadius}
      >
        <form>
          <TextField
            id="username"
            label="Email"
            variant="outlined"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            id="password"
            label="Password"
            variant="outlined"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <Box py={0}>
            <Grid container spacing={1}>
              <Grid item xs={6}>
                {/* <FormControlLabel
                  control={
                    <Checkbox name="checkedB" color="primary" size="small" />
                  }
                  label="Keep me Logged In"
                /> */}
              </Grid>
              <Grid item xs={6}>
                <Box textAlign="right">
                  <Link to="reset-password">
                    <Box lineHeight="38px">Forgot password?</Box>
                  </Link>
                </Box>
              </Grid>
            </Grid>
          </Box>
          <Box py={3} textAlign="center">
            <Button
              disabled={loggingIn}
              variant="contained"
              color="primary"
              onClick={onClick}
            >
              Login
            </Button>
          </Box>
        </form>
      </Box>
    </Box>
  );
}
