import { Box, Link, Badge } from "@material-ui/core";
import { Link as RouterLink } from "@reach/router";
import React, { ReactElement } from "react";
import { INavItem } from "estreeh-common-library/src/interfaces/INavItem";

interface Props {
  navItem: INavItem;
  my: number;
  mx: number;
  first?: boolean;
}

export default function LeftNavItem(props: Props): ReactElement {
  const first = props.first !== undefined ? props.first : true;
  const nav = props.navItem;
  const itemStyles: React.CSSProperties = {
    display: "block",
    color: "white",
    padding: "2px 8px",
    // margin: "20px 0",
    textDecoration: "none",
    fontWeight: first ? "bold" : undefined,
  };

  const hasCount = props.navItem.count !== undefined && props.navItem.count>0;

  let title: React.ReactNode = (
    <Box mr={hasCount? 1.5: 0}>
      {nav.icon && <i className={nav.icon} style={{ marginRight: 6 }}></i>}
      {nav.title}
    </Box>
  );

  let wrappedTitle = hasCount ? (<Badge badgeContent={props.navItem.count} color="secondary">{title}</Badge>) : title;

  return (
    <Box ml={props.mx} my={props.my}>
      {nav.to && (
        <RouterLink style={{ ...itemStyles }} to={nav.to}>
          {wrappedTitle}
        </RouterLink>
      )}
      {!nav.to && (
        <Link
          href="#"
          style={{
            ...itemStyles,
          }}
        >
          {title}
        </Link>
      )}
      {props.navItem.children !== undefined && (
        <Box fontSize="90%">
          {props.navItem.children?.map((navItem) => (
            <LeftNavItem
              my={0.5}
              mx={3}
              navItem={navItem}
              key={navItem.title + "_" + navItem.to}
              first={false}
            />
          ))}
        </Box>
      )}
    </Box>
  );
}
