import { Box, Grid } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement } from "react";
import { IUser } from "estreeh-common-library/src/interfaces/IUser";
import LeftNav from "../navigation/LeftNav";
import TopNav from "../navigation/TopNav";
import { useStoreState } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {
  user: IUser;
  // setUser: React.Dispatch<React.SetStateAction<IUser | undefined>>;
  children?: React.ReactNode;
}

export default function AdminPanelLayout(props: Props): ReactElement {

  const dashSummary = useStoreState(s => s.dashboardSummary.item);

  return (
    <Box height="100%">
      <Grid
        container
        direction="column"
        style={{
          height: "100%",
        }}
      >
        <Grid item xs={12}>
          <Grid
            container
            alignItems="stretch"
            style={{
              height: "100%",
            }}
          >
            <Grid item md={2} xs={2}>
              <LeftNav countRequests={dashSummary?.pendingRequests ?? 0} countWorkers={dashSummary?.myWorkers ?? 0} />
            </Grid>
            <Grid item md={10} xs={10}>
              <TopNav user={props.user}/>
              <Box px={2} pb={2} pt={12}>
                {props.children}
              </Box>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Box>
  );
}
