import { Checkbox, FormControlLabel, FormGroup } from "@material-ui/core";
import { FormikProps } from "formik";
import React, { ReactElement, useEffect, useState } from "react";

interface Props {
  options: string[];
  formik: FormikProps<any>;
  arrayFieldName: string;
}

interface Cb {
  name: string;
  label: string;
  key: string;
  checked: boolean;
  onClick: React.MouseEventHandler<HTMLButtonElement>;
}

export default function BackendCheckboxGroup({
  options,
  formik,
  arrayFieldName,
}: Props): ReactElement {
  const defaultValues: string[] = formik.values[arrayFieldName];

  useEffect(() => {
    setValues(
      options.map((d) => {
        const checked = defaultValues.includes(d);
        //TODO: (Future) optimize maybe? Do we need to re-create the onclick each time?
        return {
          name: d,
          checked,
          label: d,
          key: d,
          onClick: (e) => {
            const arr: string[] = formik.values[arrayFieldName];
            if (checked) {
              arr.splice(arr.indexOf(d, 0), 1);
            } else {
              arr.push(d);
            }
            const obj: any = { ...formik.values };
            obj[arrayFieldName] = arr;
            formik.setValues(obj);
          },
        };
      })
    );
  }, [arrayFieldName, defaultValues, formik, options]);

  const [values, setValues] = useState<Cb[]>([]);
  console.log(defaultValues);
  return (
    <FormGroup row>
      {values.map((value) => (
        <FormControlLabel
          control={
            <Checkbox
              name={arrayFieldName + "_" + value.name}
              color="primary"
              onClick={value.onClick}
              checked={value.checked}
            />
          }
          label={value.label}
          name={arrayFieldName + "_Lbl_" + value.name}
          key={value.key}
        />
      ))}
    </FormGroup>
  );
}
