import { action, Actions, createStore, thunk } from "easy-peasy";

// import _ from "lodash";

import { Base_ModelSingular } from "estreeh-common-library/src/interfaces/store/Base_ModelSingular";
// import { IBasicFilterableModel } from "estreeh-common-library/src/interfaces/store/IBasicFilterableModel";
// import { Base_Model } from "estreeh-common-library/src/interfaces/store/Base_Model";
import { toast } from "react-toastify";
import { IBeStoreModel } from "../interfaces/IBeStoreModel";
import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { IUser } from "estreeh-common-library/src/interfaces/IUser";
import { IApiResConfig } from "estreeh-common-library/src/interfaces/api/res/IApiResConfig";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import { IApiResDashboardSummary } from "estreeh-common-library/src/interfaces/api/res/IApiResDashboardSummary";
import { IApiReqLogin } from "estreeh-common-library/src/interfaces/api/req/IApiReqLogin";

const loadUser = thunk(async (actions: Actions<IBeStoreModel>) => {
  let user: IUser | undefined = undefined;
  try {
    const userResponse = await axios.get<IResponse<IUser | undefined>>(
      process.env.REACT_APP_API_ROOT + "/auth/me/agency-be",
      { withCredentials: true }
    );
    user = userResponse.data.data;
  } catch (ex) {}
  if (user !== undefined) {
    actions.user.setItem(user);
    toast.success(`Welcome, ${user.fullName}!`);
  }
  return user;
});

const loadAgency = thunk(async (actions: Actions<IBeStoreModel>) => {
  const response = await axios.get<IResponse<IAgency>>(
    process.env.REACT_APP_API_ROOT + "/be/agency",
    { withCredentials: true }
  );
  actions.agency.setItem(response.data.data);
});

const loadConfig = thunk(async (actions: Actions<IBeStoreModel>) => {
  const response = await axios.get<IResponse<IApiResConfig>>(
    process.env.REACT_APP_API_ROOT + "/pub/config",
    { withCredentials: true }
  );
  actions.config.setItem(response.data.data);
});

const loadRequests = thunk(async (actions: Actions<IBeStoreModel>) => {});

const loadWorkers = thunk(async (actions: Actions<IBeStoreModel>) => {});

const loadDashboardSummary = thunk(async (actions: Actions<IBeStoreModel>) => {
  const response = await axios.get<IResponse<IApiResDashboardSummary>>(
    process.env.REACT_APP_API_ROOT + "/be/dashboard",
    { withCredentials: true }
  );
  actions.dashboardSummary.setItem(response.data.data);
});

const loadAllInit = thunk(async (actions: Actions<IBeStoreModel>) => {
  actions.dataLoading.setItem(true);

  const [user] = await Promise.all([actions.loadUser(), actions.loadConfig()]);
  if (user !== undefined) {
    await Promise.all([
      actions.loadAgency(),
      actions.loadDashboardSummary(),
      actions.loadAgency(),
    ]);
  }
  actions.dataLoading.setItem(false);
  actions.dataLoaded.setItem(true);
});

const login = thunk(
  async (actions: Actions<IBeStoreModel>, payload: IApiReqLogin) => {
    const result = await axios.post<IResponse<IUser | undefined>>(
      process.env.REACT_APP_API_ROOT + "/auth/login",
      payload,
      { withCredentials: true }
    );
    const user = result.data.data;
    if (user !== undefined) {
      actions.dataLoaded.setItem(false);
      // actions.loadUser();
    }
    return user;
  }
);

const logout = thunk(async (actions: Actions<IBeStoreModel>) => {
  const result = await axios.get<IResponse<boolean>>(
    process.env.REACT_APP_API_ROOT + "/auth/logout",
    { withCredentials: true }
  );
  actions.user.setItem(undefined);
  const success = result.data.data;
  return success;
});

const makeStoreDefault = (): IBeStoreModel => {
  return {
    // selectedWorkerTwoYear: twoYear(),
    // selectedSlots: slots(),
    // selectedWorkerRental: hourly(),
    // translationPack: singleModel(),
    // transformData: transformData,

    // workerTwoYear: arrayModel(),
    // requests: arrayModel(),

    agency: singleModel(),
    user: singleModel(),
    config: singleModel(),
    dashboardSummary: singleModel(),
    dataLoading: singleModel(false),
    dataLoaded: singleModel(false),
    loadAllInit,
    loadUser,
    loadAgency,
    loadConfig,
    loadRequests,
    loadDashboardSummary,
    loadWorkers,
    login,
    logout,
  };
};

const singleModel = <T>(itemDefault?: T): Base_ModelSingular<T | any> => {
  return {
    item: itemDefault,
    setItem: action((state: any, payload) => {
      state.item = payload;
    }),
  };
};

