import { Box, Button } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import axios from "axios";
import React, { ReactElement, useEffect, useState } from "react";
import { IProcessTemplate } from "estreeh-common-library/src/interfaces/IProcessTemplate";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { Regions } from "../../config/Regions";
// import { ProcessTemplateFactory } from "../../factories/ProcessTemplateFactory";
import PageTitle from "../common/PageTitle";
import SubmitButton from "../forms/SubmitButton";
import ProcessTemplate from "./ProcessTemplate";

interface Props extends RouteComponentProps {}

export default function AgencyProcessTemplatesPage(props: Props): ReactElement {
  const makeBlankProcess = (): IProcessTemplate => {
    return {
      accommodationFeesBhd: 0,
      insuranceFeesBhd: 0,
      otherFeesBhd: 0,
      processFeesBhd: 0,
      processDurationMaximumMonths: 0,
      processDurationMinimumMonths: 0,
      vatFeesBhd: 0,
      governmentFeesBhd: 0,
      vatPercentage: 0,
      title: "",
      agencyId: "",
      otherFeesLabel: "Others",
      countAssignedWorkers: 0,
      totalFeesBhd: 0,
      created: new Date(),
      modified: new Date(),
    };
  };
  // const fakeProcesses: IProcessTemplate[] = ProcessTemplateFactory.buildList(0);

  // const [processes, setProcesses] = useState([...fakeProcesses, makeBlankProcess()]);
  const [processes, setProcesses] = useState<IProcessTemplate[]>([]);
  const [loading, setLoading] = useState(true);

  const addClicked = () => {
    setProcesses((prev) => [...prev, makeBlankProcess()]);
  };

  useEffect(() => {
    if (loading) {
      console.log("Loading process templates...");
      axios
        .get<IResponse<IProcessTemplate[]>>(
          process.env.REACT_APP_API_ROOT + "/be/process-templates",
          { withCredentials: true }
        )
        .then((result) => {
          setProcesses(result.data.data);
          console.log("Process templates loaded");
          setLoading(false);
          if (result.data.data.length === 0) addClicked();
        });
    }
  });

  const addMore = (
    <Box>
      <Button color="primary" variant="contained" onClick={addClicked}>
        + Add More
      </Button>
    </Box>
  );

  return (
    <Box>
      <PageTitle title="Process Templates" />
      {/* {addMore} */}
      <Box>
        {processes.map((process, index) => (
          <ProcessTemplate
            key={process._id}
            process={process}
            region={Regions.bahrain}
            newItem={process._id === undefined}
          />
        ))}
      </Box>
      {addMore}
      <SubmitButton
        title="Exit"
        navigate={props.navigate}
        navigateUrl="/panel/dashboard"
      />
    </Box>
  );
}
